.navigation {
  display: block;
}

@media (max-width: @mobile-width-only) {
  .navigation__list {
    position: relative;
    top: 80px;
    background-color: @color-wite;
    z-index: 1;
  }

  .navigation--close .navigation__wrapper {
    display: none;
  }

  .navigation__legend {
    display: none;
  }
}

.navigation__toggle {
  position: absolute;
  right: 30px;
  top: 50%;
  transform: translateY(-50%);
}

.navigation--nojs .navigation__list {
  display: block;
}

.navigation--nojs .navigation__wrapper {
  display: block;
  position: static;
}

.navigation--nojs .toggle {
  display: none;
}

@media (min-width: @tablet-width) {
  .navigation__list {
    display: flex;
    flex-wrap: wrap;
  }

  .navigation__legend {
    margin-left: auto;
    order: 3;
  }
}
