.user-list__item {
  list-style-type: none;
  box-shadow:
    0 1px 0 0 rgba(236, 236, 236, 1),
    inset 0 1px 0 0 rgba(236, 236, 236, 1);
}

.user-list__link {
  display: block;
  padding: 28px 30px 26px 76px;
  font-family: "Open Sans", "Arial", sans-serif;
  font-weight: 700;
  font-size: 17px;
  line-height: 24px;
  color: @color-black;
  text-decoration: none;
}

@media (min-width: @mobile-width-only) {
  .user-list__item {
    box-shadow: none;
  }
}

@media (min-width: @tablet-width) {
  .user-list {
    margin-left: auto;
    order: 1;
  }

  .user-list__link--search {
    width: 82px;
    height: 100%;
    padding: 0;
    border-left: 1px solid @color-light-grey;
    border-right: 1px solid @color-light-grey;
  }

  .user-list__link--text {
    display: none;
  }
}
